@import url(https://fonts.googleapis.com/css?family=Mali|Pacifico&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.card {
	text-align: left;
	font-family: 'Mali', cursive;
	color: #280868;
	background-color: #ffffff;
	border-radius: 10px;
	box-shadow: 10px 10px 0px 0px #78c0a8;
}

.card-body > h5,
.card-body > h6 {
	cursor: pointer;
}

.Todo-Completed {
	text-decoration: line-through;
	background-color: #efefef;
	box-shadow: 10px 10px 0px 0px #78c0a8;
}

.card button {
	border: none;
}

.NewTodoForm button {
     background-color: rgba(247, 124, 37, 0.75);
     border-color: rgba(247, 124, 37, 0.75);
}

.NewTodoForm button:hover {
     background-color: rgba(247, 124, 37, 1);
     border-color: rgba(247, 124, 37, 1);
}

.NewTodoForm button:focus {
     box-shadow: none;
}

.NewTodoForm input, .NewTodoForm button {
     font-family: 'Mali', cursive;
}

